.fbr-icon {
  display: inline-block;
  fill: currentColor;
  width: $icon-size;
  height: $icon-size;
}

@keyframes foobar-rotating {
  from { transform: rotate(0); }
  to { transform: rotate(360deg); }
}

.fbr-loader {
  .fbr-icon-spinner {
	animation: foobar-rotating 2s linear infinite;
  }
}
