[class^="fbr-"],
[class*=" fbr-"] {
  box-sizing: border-box;
}


.fbr-transitions-disabled {
  transition: none !important;
  * {
	transition: none !important;
  }
}

.foobar-push-transition {
  transition-property: margin, padding;
  transition-duration: 0.3s;
  transition-timing-function: ease-in-out;
}

.fbr-push-content {
  display: block;
  width: 100%;
  min-height: 0;
  height: 0;
  margin: 0;
  padding: 0;
  clear: both;
  visibility: hidden;
  opacity: 0;
  transition: height 0.3s ease-in-out;
  &.fbr-closed {
	min-height: 0!important;
	height: 0!important;
  }
}

.foobar {
  position: relative;
  display: none;
  width: auto;
  height: auto;
  min-height: $toggle-size;
  max-width: 100%;
  max-height: 100%;
  margin: 0;
  padding: 0;
  box-shadow: none;
  border: none;
  line-height: initial;
  font-family: -apple-system,system-ui,BlinkMacSystemFont,"Segoe UI",Roboto,"Helvetica Neue",Arial,sans-serif;
  font-size: 14px;
  box-sizing: border-box;
  z-index: 99998;
  pointer-events: none;
  visibility: hidden;
  opacity: 0;

  &.fbr-initialized {
	visibility: visible;
	opacity: 1;
  }

  .fbr-inner {
	position: relative;
	width: 100%;
	height: 100%;
	padding: 0;
	margin: 0;
  }

  .fbr-content {
	position: relative;
	display: flex;
	width: 100%;
	height: 100%;
	visibility: visible;
	opacity: 1;
	overflow: hidden;
	z-index: 1;
	pointer-events: auto;
  }
  &.fbr-closed {
	.fbr-content {
	  visibility: hidden;
	  opacity: 0;
	}
  }
}
