.fbr-item {
  display: flex;
  flex-wrap: nowrap;
  justify-content: center;
  align-items: center;
  align-content: center;
  text-align: center;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;

  .fbr-loader {
	display: flex;
	align-content: center;
	align-items: center;
	justify-content: center;
	position: absolute;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	width: 100%;
	height: 100%;
	margin: 0;
	padding: 0;
	visibility: hidden;
	opacity: 0;
	pointer-events: none;
	&.fbr-loading {
	  visibility: visible;
	  opacity: 1;
	}
  }

  .fbr-item-inner {
	display: inline-block;
	overflow: hidden;
	width: auto;
	height: auto;
	max-width: 100%;
	max-height: 100%;
	margin: 0;
	padding: 0;
	visibility: visible;
	opacity: 1;
	&.fbr-loading {
	  visibility: hidden;
	  opacity: 0;
	}
  }
}

.fbr-items {
  display: block;
  position: relative;
  width: 100%;
  height: 100%;
  list-style: none;
  margin: 0;
  padding: 0;

  .fbr-item {
	list-style-type: none;
	visibility: hidden;
	opacity: 0;
	z-index: 1;

	&.fbr-active {
	  visibility: visible;
	  opacity: 1;
	  z-index: 2;
	}
  }
}
