.fbr-message {
  display: inline-block;
  padding: $m-sm;
  max-width: 100%;
}

.fbr-item.fbr-buttons-item {
  .fbr-item-inner {
	display: flex;
	justify-content: center;
	align-content: center;
	align-items: center;
	flex-wrap: nowrap;
	flex-direction: row;
  }
}

.fbr-buttons-item.fbr-buttons-right {
  .fbr-item-inner {
	flex-direction: row;
  }
}

.fbr-buttons-item.fbr-buttons-left {
  .fbr-item-inner {
	flex-direction: row-reverse;
  }
}

.fbr-buttons {
  display: block;
  padding: $m-sm;
}

.fbr-buttons-inner {
  display: flex;
  justify-content: center;
  align-content: center;
  align-items: center;
  flex-wrap: nowrap;
  flex-direction: row;
}

.fbr-button {
  @include button-reset($font-size: 14px, $padding: 0 $m-md, $margin: 0 $m-0);
  min-width: 100px;
  min-height: 32px;
  line-height: 2.30769231;
  border-radius: 4px;
  white-space: nowrap;
  transition-property: background-color, color, border-color, box-shadow, transform;
  transition-timing-function: ease-in-out;
  transition-duration: 0.2s;
}

@media only screen and (max-width: $screen-md) {
  .fbr-item.fbr-buttons-item {
	.fbr-item-inner {
	  flex-direction: column;
	  .fbr-message {
		padding-bottom: $m-xs;
	  }
	}
  }
  .fbr-message + .fbr-buttons {
	padding-top: 0;
  }
  .fbr-buttons {
	width: 100%;
	padding-left: 0;
	padding-right: 0;
  }
  .fbr-buttons-inner {
	width: 100%;
	padding: 0 $m-sm;
	flex-wrap: wrap;
  }
  .fbr-button {
	min-width: 0;
	margin-top: $m-xs;
  }
  .fbr-button-mobile-100 {
	width: calc(100% - #{$m-0 * 2});
  }
  .fbr-button-mobile-50 {
	width: calc(50% - #{$m-0 * 2});
  }
  .fbr-button-mobile-33 {
	width: calc(33.3% - #{$m-0 * 2});
  }
}
